import { useState } from 'react';
import {
  Box,
  Button,
  CircularProgress,
  FormControl,
  FormHelperText,
  Grid,
  IconButton,
  InputAdornment,
  Typography
} from '@mui/material';
import {
  useForm,
  Controller
} from 'react-hook-form';
import { BootstrapInput } from '../../components/input/Input';
import Visibility from '@mui/icons-material/Visibility';
import VisibilityOff from '@mui/icons-material/VisibilityOff';
import { ILogin } from './interface';
import { yupResolver } from '@hookform/resolvers/yup';
import { schema } from './schema';
import EmailOutlinedIcon from '@mui/icons-material/EmailOutlined';
import companyLogo from '../../../src/img/company.png'
import { Link, useNavigate } from 'react-router-dom';
import { ROUTES } from '../../../core/routes/routes';
import { usersList } from '../../../mocks';
import { toast } from 'react-toastify';
import { useDispatch } from 'react-redux';
import { usersLoaded } from './user_slice';

const Login = () => {
  const [showPassword, setShowPassword] = useState<boolean>(false);
  const [loggingIn, setLoggingIn] = useState<boolean>(false);
  const dispatch = useDispatch()

  const navigate = useNavigate();

  const handleClickShowPassword = () => setShowPassword((show) => !show);
  const handleMouseDownPassword = (event: React.MouseEvent<HTMLButtonElement>) => {
    event.preventDefault();
  };

  const {
    control,
    handleSubmit,
    formState: { errors }
  } = useForm<ILogin>({
    mode: 'onChange',
    resolver: yupResolver(schema)
  });

  const onSubmit = (formData: ILogin) => {
    setLoggingIn(true);

    const user = usersList.find(person => formData.email === person.email);

    if (user) {
      if (user.password === formData.password) {
        navigate(ROUTES.DASHBOARD)
        toast.success(`Welcome ${user.firstName}`);
        dispatch(usersLoaded({ user }) as any)
        return
      }
      toast.error('Invalid User Credentials');
      return
    }
    setTimeout(() => {
      setLoggingIn(false);
    }, 2000);
    return toast.error('Invalid User Credentials')
  }


  return (
    <Grid container xs={12}
      sx={{
        height: '100vh',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center'
      }}>
      <Grid xs={4} item >
        <Box
          component='div'
          sx={{ bgcolor: 'teal' }}
        >
          <form autoComplete='false'
            onSubmit={handleSubmit(onSubmit)}
          >
            <Grid container xs={12} >
              <Grid item p={2} container xs={12} sx={(theme) => ({
                bgcolor: theme.palette.background.paper,
                borderRadius: '6px'
              })}>
                <Grid item container spacing={3} xs={12} padding={4}>
                  <Grid item xs={12} sx={{ display: 'flex', justifyContent: 'center' }} >
                    < Box
                      component="img"
                      sx={{ height: '150px', width: '200px' }}
                      alt='px'
                      src={companyLogo}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <FormControl fullWidth>
                      <Controller
                        control={control}
                        name='email'
                        rules={{ required: true }}
                        render={({ field: { onChange, onBlur } }) => (
                          <BootstrapInput
                            size='small'
                            id='email'
                            label='Email Address'
                            variant="outlined"
                            onBlur={onBlur}
                            onChange={onChange}
                            error={Boolean(errors.email)}
                            InputProps={{
                              startAdornment: (
                                <InputAdornment position="start">
                                  <EmailOutlinedIcon />
                                </InputAdornment>
                              ),
                            }}
                          />
                        )}
                      />
                      {errors.email && (
                        <FormHelperText sx={{ color: 'error.main' }}>{errors.email.message}</FormHelperText>
                      )}
                    </FormControl>
                  </Grid>
                  <Grid item xs={12}>
                    <FormControl fullWidth>
                      <Controller
                        control={control}
                        name='password'
                        rules={{ required: true }}
                        render={({ field: { onChange, onBlur } }) => (
                          <BootstrapInput
                            size='small'
                            id='password'
                            label='Password'
                            onBlur={onBlur}
                            onChange={onChange}
                            error={Boolean(errors.password)}
                            type={showPassword ? 'text' : 'password'}
                            InputProps={{
                              endAdornment: (
                                <InputAdornment position="end">
                                  <IconButton
                                    aria-label="toggle password visibility"
                                    onClick={handleClickShowPassword}
                                    onMouseDown={handleMouseDownPassword}
                                    edge="end"
                                  >
                                    {showPassword ? <VisibilityOff /> : <Visibility />}
                                  </IconButton>
                                </InputAdornment>
                              ),
                            }}
                          />
                        )}
                      />
                      {errors.password && (
                        <FormHelperText sx={{ color: 'error.main' }}>{errors.password.message}</FormHelperText>
                      )}
                    </FormControl>
                  </Grid>
                  <Grid item xs={12}>
                    <Box component='div'>
                      <Button
                        type='submit'
                        variant='contained'
                        fullWidth
                        size='large'
                        sx={{
                          textTransform: 'none'
                        }}
                        startIcon={
                          loggingIn ? (
                            <CircularProgress />
                          ) : ('')
                        }
                        disabled={loggingIn}
                      >
                        Log in
                      </Button>
                    </Box>
                  </Grid>
                  <Grid item xs={12}>
                    <Typography sx={{ fontSize: '14px', textAlign: 'center' }}>
                      Sign Up <Link style={{ textDecoration: 'none', color: 'blue' }} to={ROUTES.REGISTER}> here</Link>
                    </Typography>
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
          </form>
        </Box>
      </Grid>
    </Grid>
  )
}

export default Login;